label {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 30px;
  margin-bottom: 5px;
}

#main-in {
  opacity: 0;
  width: 0;
  height: 0;
}

span {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #2c3e50;
  transition: 0.3s;
  border-radius: 30px;
}
span:before {
  position: absolute;
  content: "";
  height: 25px;
  width: 25px;
  left: 3px;
  bottom: 2.6px;
  background-color: #fff;
  border-radius: 50%;
  transition: 0.3s;
}

#main-in:checked + span {
  background-color: #1976D2;
}

#main-in:checked + span:before {
  transform: translateX(29px);
}
strong {
  position: absolute;
  left: 100%;
  width: max-content;
  line-height: 30px;
  margin-left: 10px;
  cursor: pointer;
  font-weight: 300;
}

.inner-con{
  padding: 50px;
  border: 2px solid #d3d3d3;
  width: 40%;
}

.main-con{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.main-btn{
  background-color:#1976D2;
  margin-top: 20px;
  padding: 30px;
  border: none;
  color: white;
}